<template>
  <div id="elm-lp-app" class="center">
    <!-- <div class="comp-title" v-html="$t('title')"></div> -->
    <!-- <div class="description"><p>{{$t('description')}}</p></div>  -->
    <!-- <button @click="frenchChartData">
      French Test {{ this.$i18n.locale }}
    </button> -->
    <!-- <LanguageSwitcher v-if="this.$store.state.showlang && !isProduction" /> -->
    <LanguageSwitcher v-if="this.$store.state.showlang" />

    <b-alert
      v-if="checkIsEdge()"
      show
      variant="info"
      style="margin-bottom: 0px;"
      dismissible
      >{{ $t("browser_warning") }}</b-alert
    >
    <div class="main---wrapper">
      <template v-for="item in allFilters">
        <FilterGroup :cat="item" :key="item" :class="item" class="group" />
      </template>
      <b-row class="chart--toolbar mb-2">
        <b-col>
          <!-- Navigation -->
          <b-button-toolbar id="chart-nav" class="toolbar-nav">
            <b-button-group class="mx-1" size="md">
              <b-button
                @click="changeChartType('earnings_level')"
                class="toolbar-nav"
                :class="{ active: chartType == 'earnings_level' }"
                >{{ $t("earnings_level") }}</b-button
              >
              <b-button
                @click="changeChartType('earnings_growth')"
                class="toolbar-nav"
                :class="{ active: chartType == 'earnings_growth' }"
                >{{ $t("earnings_growth") }}</b-button
              >
            </b-button-group>
          </b-button-toolbar>
        </b-col>
        <b-col class="toolbar-right">
          <!-- Toolbar -->
          <b-button-toolbar class="toolbar-chart float-right">
            <b-button-group class="mx-1" size="sm">
              <b-button v-on:click="resetFilters">{{
                $t("Reset Filters")
              }}</b-button>
              <b-button class="reset-range" v-on:click="resetRangeSlider">{{
                $t("Reset Range")
              }}</b-button>

              <!-- share button -->
              <b-button
                v-clipboard:copy="chartUrl"
                v-clipboard:success="onCopy"
                v-clipboard:error="onCopyError"
                id="share-url"
                class="btn-share"
                v-b-tooltip.hover
                :title="[$t('share_button')]"
              >
                <font-awesome-icon icon="share-alt" />
              </b-button>
              <b-popover
                target="share-url"
                ref="shareUrl"
                triggers="click blur"
                placement="bottom"
              >
                <template v-slot:title>Copy URL</template>
                <b-form-input
                  id="share-url"
                  @focus="$event.target.select()"
                  v-model="chartUrl"
                  placeholder="Chart URL"
                  style="margin-bottom: .8rem;"
                />
                <b-alert v-if="copyUrlSuccess" variant="success" show
                  >Successfully copied to clipboard!</b-alert
                >
              </b-popover>

              <!-- screenshot button / hide if mobile -->
              <b-button
                v-if="!isMobile()"
                v-on:click="getChartImage"
                v-b-tooltip.hover
                :title="[$t('dl_image_button')]"
              >
                <font-awesome-icon icon="camera" />
              </b-button>

              <!-- download data files dropdown -->
              <b-dropdown
                id="download-data"
                v-b-tooltip.hover
                :title="[$t('dl_data_button')]"
                class="btn-share"
                right
              >
                <template slot="button-content">
                  <font-awesome-icon icon="download" />
                </template>
                <b-dropdown-text style="font-weight: 800; width: 175px;">{{
                  $t("dl_data_options.dl_header")
                }}</b-dropdown-text>
                <b-dropdown-divider></b-dropdown-divider>
                <b-dropdown-item v-b-tooltip.hover title>
                  <!-- download chart data -->
                  <vue-json-to-csv
                    class="download-wrapper"
                    :json-data="csvChartData"
                    :csv-title="csvTitle"
                    :labels="csvLabels"
                    >{{ $t("dl_data_options.dl_data_chart") }}</vue-json-to-csv
                  >
                </b-dropdown-item>
                <b-dropdown-item v-b-tooltip.hover title>
                  <!-- download chart data -->
                  <vue-json-to-csv
                    class="download-wrapper"
                    :json-data="data"
                    :csv-title="downloadAll.csv.label"
                    :labels="csvLabels"
                    >{{ $t("dl_data_options.dl_data_csv") }}</vue-json-to-csv
                  >
                </b-dropdown-item>
                <b-dropdown-item v-b-tooltip.hover title>
                  <!-- download chart data as exel-->
                  <json-excel
                    :data="data"
                    :name="downloadAll.xls.label"
                    type="xls"
                    worksheet="Shee1"
                  >
                    {{ $t("dl_data_options.dl_data_xlsx") }}
                  </json-excel>
                </b-dropdown-item>
              </b-dropdown>
            </b-button-group>
          </b-button-toolbar>
        </b-col>
      </b-row>
      <div class="chart--wrapper">
        <!-- <b-alert v-if="chartType == 'earnings_growth'" show dismissible>{{
          $t("no2014growth")
        }}</b-alert> -->
        <b-alert
          :show="gender_immcode_warning_show"
          @dismissed="gender_immcode_warning_show = false"
          dismissible
          variant="primary"
          >{{ $t("sex_intl_warning") }}</b-alert
        >
        <!-- <router-view /> -->
        <b-card id="chart-frame" class="">
          <Chart :chartType="chartType" />
        </b-card>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import LanguageSwitcher from "@/components/LanguageSwitcher.vue";
import FilterGroup from "@/components/FilterGroup.vue";
import Chart from "@/components/Chart.vue";

import VueJsonToCsv from "vue-json-to-csv";
import noUiSlider from "nouislider";
import VueApexCharts from "vue-apexcharts";
import domtoimage from "dom-to-image";
import { saveAs } from "file-saver";
// import ClipboardJS from "clipboard";
import Axios from "axios";
import JsonExcel from "vue-json-excel";

export default {
  name: "psis-chart",
  props: {
    lang: {
      default: "en",
      type: String,
    },
  },
  components: {
    LanguageSwitcher,
    FilterGroup,
    Chart,
    VueJsonToCsv,
    VueApexCharts,
    JsonExcel,
  },
  watch: {
    "$i18n.locale": function() {
      // update download menu on lang change
      this.initDownloadMenu();
    },
  },
  data: () => {
    return {
      isProduction: process.env.NODE_ENV === "production",
      allFilters: [
        "cohort",
        "fos",
        "pse_credential",
        "gender",
        "immcode",
        "bos",
      ],
      bosFilters: ["fos"],
      copiedUrl: false,
      currentUrl: "",
      chartUrl: "",
      chartType: "earnings_level",
      defaultFiltersObj: {
        pse_credential: [
          "College-level Certificate",
          "College-level Diploma",
          "Master's Degree",
          "Bachelor's Degree",
          "Doctoral Degree",
          "Professional Degree",
        ],
        cohort: [2010],
        gender: ["Both sexes"],
        immcode: ["All student types"],
        fos: ["All fields of study"],
      },
      csvLabelOptions: {
        en: {
          cohort: { title: "Cohort" },
          pse_credential: { title: "PSE Credential" },
          yrs_since_grad: { title: "Years Since Grad" },
          fos: { title: "Field of Study" },
          gender: { title: "Gender" },
          immcode: { title: "IMM Code" },
          earnings_level: { title: "Earning Level" },
          earnings_growth: { title: "Earnings Growth" },
          count: { title: "Count" },
        },
        fr: {
          cohort: { title: "Cohort" },
          pse_credential: { title: "Sanction d'études" },
          yrs_since_grad: { title: "Année de l'obtention du diplôme" },
          fos: { title: "Domaine d'études" },
          gender: { title: "Sexe" },
          immcode: { title: "Code IMM" },
          earnings_level: { title: "Revenus" },
          earnings_growth: { title: "Croissances" },
          count: { title: "Nombre" },
        },
      },
      csvLabels: {},
      csvTitle: "",
      csvChartData: [],
      downloadAll: {
        csv: { fileType: "application/csv" },
        xls: { fileType: "application/vnd.ms-excel" },
      },
      componentLoaded: false,
      gender_immcode_warning_show: false,
      copyUrlSuccess: false,
    };
  },
  computed: {
    data: function() {
      return this.$store.state.data;
    },
    chartData: function() {
      //console.log("chart data", this.$store.state.newData);
      return this.$store.state.newData;
    },
    selectedFilters: {
      get: function() {
        return this.$store.getters.selectedFilters;
      },
      set: function(newVal) {
        let _selectedFilters = this.$store.getters.selectedFilters;
        _selectedFilters[this.cat] = newVal;
      },
    },
  },
  methods: {
    resetRangeSlider: function() {
      var slider = document.getElementById("vertical-range-slider");
      ApexCharts.exec(
        "elm-lp-chart",
        "updateOptions",
        {
          yaxis: {
            min: 0,
            max: 140000,
          },
        },
        false,
        true
      );
      slider.noUiSlider.reset();
    },
    resetFilters: function() {
      //this.$store.state.selectedFilters = this.defaultFiltersObj;
      this.$store.commit("resetFilters");
      console.log("resetFilters", this.$store.state.selectedFilters);
      //alert('This feature is still under development')
    },
    deselectAll: function() {
      if (window.getSelection) {
        window.getSelection().removeAllRanges();
      } else if (document.selection) {
        document.selection.empty();
      }
    },
    getChartImage: function() {
      // screen shot of chart element
      // deselect any times
      this.deselectAll;
      const chartType = this.chartType;
      const imageLabel = this.$i18n.t(`${chartType}`);
      let chartFrame = document.querySelector("#chart-frame");

      function filter(node) {
        return node.id !== "vertical-range-slider";
      }

      function deselectAll() {
        if (window.getSelection) {
          window.getSelection().removeAllRanges();
        } else if (document.selection) {
          document.selection.empty();
        }
      }

      // image URL test
      domtoimage
        .toPng(chartFrame)
        .then(function(dataUrl) {
          console.log("dataUrl", dataUrl);
        })
        .catch(function(error) {
          console.error("oops, something went wrong!", error);
        });

      domtoimage
        .toBlob(chartFrame, { filter: filter })
        .then(function(blob) {
          window.saveAs(blob, imageLabel + ".png");
        })
        .catch(function(error) {
          console.error("oops, something went wrong!", error);
        });
    },
    loadPresetsFromUrl: function() {
      let _this = this;
      this.currentUrl = window.location.href;
      let url = new URL(this.currentUrl);
      let chartType = url.searchParams.get("chartType");
      let preset = url.searchParams.get("preset");
      let rangeSliderValues = url.searchParams.get("range");

      // determin anchor point
      let anchorPoint = document.getElementById("elm-lp-app");

      //remove the __ (double underline) and replace with space
      preset = preset.replace(/__/g, " ");

      let presetObj = JSON.parse(preset);

      console.log("preset-data:", JSON.parse(preset), chartType);
      const loadFilterPresets = new Promise(function(resolve, reject) {
        // check if preset filters are properly set as an object
        // if yes load preset filters
        if (typeof presetObj == "object") {
          _this.$store.state.selectedFilters = presetObj;
        }
        resolve([chartType, presetObj]);
      });

      // load preset filters then remove the params from the url
      loadFilterPresets.then(function(value) {
        console.log("loadFilter", value);
        _this.chartType = chartType;
        setTimeout(function() {
          _this.updateRangeSlider();
        }, 3000);

        _this.cleanUrl();
      });

      anchorPoint.scrollIntoView();
    },
    generateChartUrl: function() {
      //console.log("generateURl");
      let _this = this;
      new Promise(function(resolve, reject) {
        let _selectedFilters = _this.$store.state.selectedFilters;
        let chartType = _this.chartType;
        let path = window.location.pathname || "";

        //console.log('this.componentLoaded',this.componentLoaded)
        // Range Slider Values to String
        let rangeSlider = document.querySelector("#vertical-range-slider");
        let range = rangeSlider.noUiSlider.get();
        //console.log("range", range);
        let rangeMin = range[0].replace(/[!@#$%^&*,]/gi, "");
        let rangeMax = range[1].replace(/[!@#$%^&*,]/gi, "");
        let rangeSliderValues = [rangeMin, rangeMax].toString();

        let u = new URLSearchParams(_selectedFilters).toString();
        // console.log(
        //   "_selectedFilters",
        //   _selectedFilters.pse_credential.toString()
        // );
        //creates params for ul

        let urlParam = encodeURIComponent(JSON.stringify(_selectedFilters));
        let currentUrl = window.location.origin + path;
        let chartUrl =
          currentUrl +
          "?preset=" +
          urlParam +
          "&chartType=" +
          chartType +
          "&range=" +
          rangeSliderValues +
          "&/";
        _this.chartUrl = chartUrl.replace(/%20/g, "__"); // replace %20 with __ (double underline) - causing wordpress errors
        return chartUrl;
        resolve(chartUrl);
      });
    },
    copyChartUrl: function(e) {
      let _this = this;
      this.generateChartUrl();

      setTimeout(function() {
        console.log("copy started");
        _this.$copyText(_this.chartUrl).then(
          function(e) {
            _this.copiedUrl = true;
            console.log(e);
            select();
          },
          function(e) {
            console.log("Can not copy");
            console.log(e);
            select();
          }
        );
      }, 1000);

      function select() {
        let input = document.querySelector("#share-url");
        input.focus();
        input.select();
      }
    },
    onCopy: function(e) {
      let _this = this;
      this.copyUrlSuccess = true;

      setTimeout(function() {
        _this.copyUrlSuccess = false;
        _this.$refs.shareUrl.$emit("close");
      }, 2500);
      //alert("You just copied: " + e.text);
    },
    onCopyError: function(e) {
      this.copyUrlSuccess = "failed";
      //alert("Failed to copy texts");
    },
    updateRangeSlider: function() {
      // Range Slider
      let currentUrl = this.currentUrl;
      let url = new URL(currentUrl);
      let rangeSliderValues = url.searchParams.get("range");
      let rangeSliderArray = JSON.parse("[" + rangeSliderValues + "]");
      let rangeSlider = document.querySelector("#vertical-range-slider");
      rangeSlider.noUiSlider.set(rangeSliderArray);
    },
    gender_immcode_warning_method(payload) {
      console.log("gender_immcode_warning_method", payload);
      let genderFilters = this.selectedFilters.gender;
      //console.log("genderFilters", genderFilters);
      // this checks if the warning has been shown for immcode if it hasn't show it
      if (payload.code == "immcode" && payload.show == true) {
        this.gender_immcode_warning_show = true;
      }
      if (payload.code == "immcode" && payload.show == false) {
        this.gender_immcode_warning_show = false;
        return;
      }

      // this checks if the warning has been shown for gender if it hasn't show it
      if (genderFilters.length == 1 && genderFilters.includes("Both sexes")) {
        this.gender_immcode_warning_show = false;
      } else {
        this.gender_immcode_warning_show = true;
      }
      // if (payload.code == "gender" && payload.show == true) {
      //   this.gender_immcode_warning_show = true;
      // } else if (payload.code == "gender" && payload.show == false) {
      //   this.gender_immcode_warning_show = false;
      // }

      //console.log("warning fired", payload, this.gender_immcode_warning_show);
    },
    isMobile: function() {
      var check = false;
      (function(a) {
        if (
          /(android|bb\d+|meego).+mobile|avantgo|bada\/|blackberry|blazer|compal|elaine|fennec|hiptop|iemobile|ip(hone|od)|iris|kindle|lge |maemo|midp|mmp|mobile.+firefox|netfront|opera m(ob|in)i|palm( os)?|phone|p(ixi|re)\/|plucker|pocket|psp|series(4|6)0|symbian|treo|up\.(browser|link)|vodafone|wap|windows ce|xda|xiino/i.test(
            a
          ) ||
          /1207|6310|6590|3gso|4thp|50[1-6]i|770s|802s|a wa|abac|ac(er|oo|s\-)|ai(ko|rn)|al(av|ca|co)|amoi|an(ex|ny|yw)|aptu|ar(ch|go)|as(te|us)|attw|au(di|\-m|r |s )|avan|be(ck|ll|nq)|bi(lb|rd)|bl(ac|az)|br(e|v)w|bumb|bw\-(n|u)|c55\/|capi|ccwa|cdm\-|cell|chtm|cldc|cmd\-|co(mp|nd)|craw|da(it|ll|ng)|dbte|dc\-s|devi|dica|dmob|do(c|p)o|ds(12|\-d)|el(49|ai)|em(l2|ul)|er(ic|k0)|esl8|ez([4-7]0|os|wa|ze)|fetc|fly(\-|_)|g1 u|g560|gene|gf\-5|g\-mo|go(\.w|od)|gr(ad|un)|haie|hcit|hd\-(m|p|t)|hei\-|hi(pt|ta)|hp( i|ip)|hs\-c|ht(c(\-| |_|a|g|p|s|t)|tp)|hu(aw|tc)|i\-(20|go|ma)|i230|iac( |\-|\/)|ibro|idea|ig01|ikom|im1k|inno|ipaq|iris|ja(t|v)a|jbro|jemu|jigs|kddi|keji|kgt( |\/)|klon|kpt |kwc\-|kyo(c|k)|le(no|xi)|lg( g|\/(k|l|u)|50|54|\-[a-w])|libw|lynx|m1\-w|m3ga|m50\/|ma(te|ui|xo)|mc(01|21|ca)|m\-cr|me(rc|ri)|mi(o8|oa|ts)|mmef|mo(01|02|bi|de|do|t(\-| |o|v)|zz)|mt(50|p1|v )|mwbp|mywa|n10[0-2]|n20[2-3]|n30(0|2)|n50(0|2|5)|n7(0(0|1)|10)|ne((c|m)\-|on|tf|wf|wg|wt)|nok(6|i)|nzph|o2im|op(ti|wv)|oran|owg1|p800|pan(a|d|t)|pdxg|pg(13|\-([1-8]|c))|phil|pire|pl(ay|uc)|pn\-2|po(ck|rt|se)|prox|psio|pt\-g|qa\-a|qc(07|12|21|32|60|\-[2-7]|i\-)|qtek|r380|r600|raks|rim9|ro(ve|zo)|s55\/|sa(ge|ma|mm|ms|ny|va)|sc(01|h\-|oo|p\-)|sdk\/|se(c(\-|0|1)|47|mc|nd|ri)|sgh\-|shar|sie(\-|m)|sk\-0|sl(45|id)|sm(al|ar|b3|it|t5)|so(ft|ny)|sp(01|h\-|v\-|v )|sy(01|mb)|t2(18|50)|t6(00|10|18)|ta(gt|lk)|tcl\-|tdg\-|tel(i|m)|tim\-|t\-mo|to(pl|sh)|ts(70|m\-|m3|m5)|tx\-9|up(\.b|g1|si)|utst|v400|v750|veri|vi(rg|te)|vk(40|5[0-3]|\-v)|vm40|voda|vulc|vx(52|53|60|61|70|80|81|83|85|98)|w3c(\-| )|webc|whit|wi(g |nc|nw)|wmlb|wonu|x700|yas\-|your|zeto|zte\-/i.test(
            a.substr(0, 4)
          )
        )
          check = true;
      })(navigator.userAgent || navigator.vendor || window.opera);
      return check;
    },
    checkIsEdge: function() {
      return this.$browserDetect.isEdge;
    },
    cleanUrl() {
      const url = window.location.href;
      const preset = /\?(.*)/.exec(url); //select everything after ?
      const hash = /#\//gm.exec(url); //select #
      let clearUrl = "";

      // remove hash from url
      if (hash) {
        clearUrl = url.replace(hash, "");
        history.replaceState(null, "", clearUrl);
      }
      // remove everything after ? - used to remove presets
      if (preset) {
        clearUrl = url.replace(preset[0], "");
        history.replaceState(null, "", clearUrl);
      }
    },
    changeChartType: function(type) {
      this.chartType = type;
    },
    frenchChartData() {
      const data = this.chartData;
      let newData = [];

      data.forEach((item) => {
        const _this = this;
        const selectors = [
          "pse_credential",
          "cohort",
          "immcode",
          "gender",
          "fos",
        ];
        let newItem = {};
        let keys = Object.keys(item);

        keys.forEach((key) => {
          let newKey = key + "_selectors";
          let value = item[`${key}`];

          // if key is a selector translate it
          if (selectors.includes(key)) {
            let newValue = _this.$i18n.t(`${newKey}["${value}"]`);
            newItem[key] = newValue;
          } else {
            // if the key doens't match just transfer the value
            newItem[key] = value;
          }
        });
        newData.push(newItem);
      });

      console.log("frenchData", newData);
      return newData;
    },
    initChartData() {
      //console.log("init Chart Data");
      if (this.$i18n.locale == "fr") {
        // if french translate french chart data
        this.csvChartData = this.frenchChartData();
      } else {
        // if not french use english data
        this.csvChartData = this.chartData;
      }
      return;
    },
    initDownloadMenu() {
      console.log("initDownload");
      // configures download labels and filename
      let chartType = this.chartType;

      if (this.$i18n.locale == "fr") {
        // if french translate french chart data
        this.downloadAll.csv = {
          file: "CIMT-PLEMT_toutes_les_donnees.csv",
          label: "CIMT-PLEMT_toutes_les_donnees",
        };
        this.downloadAll.xls = {
          file: "Combien_gagnent-ils-graphiques_du_rapport.xls",
          label: "Combien_gagnent-ils-graphiques_du_rapport",
        };
        this.csvLabels = this.csvLabelOptions["fr"];
        this.csvTitle = this.$i18n.t(`${chartType}`) + "_telecharger";
      } else {
        // if not french use english data
        this.downloadAll.csv = {
          file: "LMIC-ELMLP_All_data.csv",
          label: "LMIC-ELMLP_All_data",
        };
        this.downloadAll.xls = {
          file: "How_much_do_they_make-Report_Charts.xls",
          label: "How_much_do_they_make-Report_Charts",
        };
        this.csvLabels = this.csvLabelOptions["en"];
        this.csvTitle = chartType + "_download";
      }
    },
  },
  updated: function() {
    this.componentLoaded = true;
  },
  beforeMount() {
    // for testing
    let currentUrl = window.location.href;
    let url = new URL(currentUrl);

    // check for language
    let lang = url.searchParams.get("lang");
    if (lang && lang == "fr") {
      this.$root.$i18n.locale = "fr";
    }
  },
  mounted: function() {
    let _this = this;
    this.$nextTick(function() {
      this.componentLoaded = true;
      let currentUrl = window.location.href;
      let url = new URL(currentUrl);
      let chartType = url.searchParams.get("chartType");
      let preset = url.searchParams.get("preset");
      let range = url.searchParams.get("range");
      let lang_check = /(\/fr\/|\/fr)/.test(url); //returns true if /fr/ found in url;

      // check if URL has the following presets chartType / preset / range
      if (chartType && preset && range) {
        //if has presets load chart if not go to default
        this.loadPresetsFromUrl();
      }

      // check language in URL
      if (lang_check) {
        this.$root.$i18n.locale = "fr";
        this.$eventBus.$emit("initChart");
      }

      setTimeout(function() {
        _this.generateChartUrl();
        _this.initDownloadMenu();
      }, 1000);
      setTimeout(function() {
        document.querySelector(".reset-range").click();
      }, 2000);
    });

    // listen for dropdown menu event
    this.$root.$on("bv::dropdown::show", (bvEvent) => {
      console.log("Dropdown is about to be shown", bvEvent);
      this.initChartData();
    });
  },
  created() {
    // global event bus 'on'
    this.$eventBus.$on(
      "show_gender_immcode_warning",
      this.gender_immcode_warning_method
    );
    this.$eventBus.$on("updateChartUrl", this.generateChartUrl);
  },
  beforeDestroy() {
    // global event bus 'off'
    this.$eventBus.$off("show_gender_immcode_warning");
  },
};
</script>

