<template>
  <ul class="lang-switcher">
    <li v-for="lang in languages" :key="lang">
      <button class="button" @click="changeLang(lang)">{{ lang }}</button>
    </li>
  </ul>
  <!-- <div>
        <b-form-checkbox :v-model="lang" switch size="lg" @click="changeLang('fr')">English / French</b-form-checkbox>
        <h1>{{lang}}</h1>
    </div> -->
</template>

<script>
export default {
  name: "LanguageSwitcher",
  data: () => ({
    languages: ["en", "fr"],
    lang: "",
  }),
  methods: {
    changeLang(lang) {
      //console.log('lang',lang)
      this.$i18n.locale = lang;
    },
  },
};
</script>

