import VueI18n from 'vue-i18n'
import Vue from 'vue'
import en from '@/lang/en.json'
import fr from '@/lang/fr.json'


const numberFormats = {
  'en': {
    currency: {
      style: 'currency', currency: 'CAD', currencyDisplay: 'symbol', minimumFractionDigits: 0, maximumFractionDigits: 0
    }
  },
  'fr': {
    currency: {
      style: 'currency', currency: 'CAD', currencyDisplay: 'symbol', minimumFractionDigits: 0, maximumFractionDigits: 0
    }
  }
}

Vue.use(VueI18n)
export const i18n = new VueI18n({
  locale: 'en', // set locale
  fallbackLocale: 'en',
  messages: { en, fr }, // set locale messages
  silentTranslationWarn: true,
  numberFormats
})


