          ></div>
          <input
            id="fos_all"
            type="checkbox"
            name="fos_all"
            autocomplete="off"
            class="custom-control-input"
            v-model="fos_select_all"
          />
          <label for="fos_all" class="custom-control-label">{{
            $t("fos_selectors['Select All']")
          }}</label>
          <div class="etched-line"></div>
        </div>
        <div
          v-for="(item, index) in filterCategories[cat]"
          :key="index"
          class="custom-control custom-checkbox"
        >
          <input
            :id="cat + '_' + index"
