<script>
/* eslint-disable */
import stripHtml from "string-strip-html";

export default {
  name: "FilterGroup",
  props: ["cat"],
  data() {
    return {
      mouseOverTooltip: false,
      fos_select_all: false,
      fos_all_array: [
        "Education",
        "Visual and performing arts, and communication technologies",
        "Humanities",
        "Social and behavioural sciences, and law",
        "Business, management and public administration",
        "Physical and life sciences, and technologies",
        "Mathematics, computer and information sciences",
        "Architecture, engineering and related technologies",
        "Agriculture, natural resources and conservation",
        "Health and related fields",
        "Personal, protective and transportation services",
      ],
      resetSettings: null,
      disable: true,
      selectedFiltersArray: null,
    };
  },
  computed: {
    filterCategories: function() {
      return this.$store.getters.filterCategories;
    },
    selectedFilters: {
      get: function() {
        return this.$store.getters.selectedFilters;
      },
      set: function(newVal) {
        let _selectedFilters = this.$store.getters.selectedFilters;
        _selectedFilters[this.cat] = newVal;
      },
    },
  },
  methods: {
    selectionClick: function(payload) {
      // FILTER LOGIC
      this.disable = false;

      let _selectedFilters = this.selectedFilters;
      let lengthTest = _selectedFilters[this.cat].length;
      //console.log("gender:", _selectedFilters["gender"]);
      console.log("payload", payload);
      let bothSexesChecked = document.querySelector("#gender_0").checked; // determines if 'Both Sexes' has been checked (boolean)
      let allStudentTypesChecked = document.querySelector("#immcode_0").checked; // determines if 'All Student Types' has been checked (boolean)

      // checks to make sure at least 1 item is selected in the Filter Group
      if (lengthTest == 1) {
        document.querySelector(`#${payload.id}`).checked = true;
      }

      // GENDER LOGIC
      // If Male or Female is selected. Deselect 'Both Sexes'
      if (
        bothSexesChecked &&
        lengthTest == 1 &&
        payload.filterGroup == "gender" &&
        payload.select !== "Both sexes"
      ) {
        _selectedFilters["gender"] = [payload.select];
      }

      // GENDER / IMMCODE Relational logic
      // if any other Gender selector other than All is selected set immcode to All
      if (payload.filterGroup == "gender" && payload.select !== "Both sexes") {
        _selectedFilters["immcode"] = ["All student types"];
        this.$eventBus.$emit("show_gender_immcode_warning", {
          code: "immcode",
          show: true,
        });
      }

      if (
        (payload.select == "Male" || payload.select == "Female") &&
        payload.checked == false
      ) {
        // If Both sexes is selected hide warning
        console.log("turn gender warning off");
        this.$eventBus.$emit("show_gender_immcode_warning", {
          code: "immcode",
          show: false,
        });
      }
      if (_selectedFilters["gender"].includes("Both sexes")) {
        this.$eventBus.$emit("show_gender_immcode_warning", {
          code: "immcode",
          show: false,
        });
      }
      // if any other Immcode selector other than All is selected set Gender to All
      if (
        payload.filterGroup == "immcode" &&
        payload.select !== "All student types"
      ) {
        _selectedFilters["gender"] = ["Both sexes"];
        this.$eventBus.$emit("show_gender_immcode_warning", {
          code: "gender",
          show: true,
        });
      }

      // IMMCODE LOGIC
      // If any other 'Canadian Students' or 'International Students' are selected deslect 'All Student types'
      if (
        allStudentTypesChecked &&
        lengthTest == 1 &&
        payload.filterGroup == "immcode" &&
        payload.select !== "All student types"
      ) {
        _selectedFilters["immcode"] = [payload.select];
      }
    },
    closeTooltip: function() {
      console.log("close tooltip");
      let _this = this;
      let mouseOverTooltip = this.mouseOverTooltip;

      setTimeout(function() {
        // close all tooltips if mouse is not over a tooltip
        if (!mouseOverTooltip) {
          _this.$root.$emit("bv::hide::popover");
          _this.$root.$emit("bv::hide::tooltip");
        }
      }, 500);
    },
    noHtml: function(string) {
      return stripHtml(string);
    },
    init() {
      this.resetSettings = this.selectedFilters[this.cat];
      // remove BHASE and STEM from from 'Fields of Study' category
      let FOS_STEM = document.querySelector('input[id^="fos_"][value="STEM"]');
      let FOS_BHASE = document.querySelector(
        'input[id^="fos_"][value="BHASE"]'
      );
      if (FOS_STEM) {
        document
          .querySelector('input[id^="fos_"][value="STEM"]')
          .parentElement.remove();
      }
      if (FOS_BHASE) {
        document
          .querySelector('input[id^="fos_"][value="BHASE"]')
          .parentElement.remove();
      }
    },
    reset() {
      this.disable = true;
      let cat = this.cat;
      this.$store.state.selectedFilters[cat] = this.resetSettings;
    },
  },
  watch: {
    fos_select_all: function() {
      // select all fos
      if (this.fos_select_all) {
        let fos_all_array = this.fos_all_array;
        this.selectedFilters["fos"] = fos_all_array;
        console.log("fos all", fos_all_array);
      } else if (!this.fos_select_all) {
        // only select 'All fields of study'
        this.selectedFilters["fos"] = ["All fields of study"];
      }
    },
  },
  mounted: function() {
    this.$nextTick(function() {
      this.init();

      //this.$store.commit("filteredData");
      // Code that will run only after the
      // entire view has been rendered
    });
  },
};
</script>

