          // ... but no more than 600
          //limit: 600,

          // Display colored bars between handles
          connect: true,

          // Put '0' at the bottom of the slider
          direction: "rtl",
          orientation: "vertical",

          // Move handle on tap, bars are draggable
          behaviour: "tap-drag",
          tooltips: true,
          format: {
            to: function(value) {
              //let _value=(value * 100).toFixed(0)
              return `${value.toFixed(0)}%`;
