  },
  computed: {
    selectedFilters: function() {
      // SORT selected filters to match the order of the filters rendered on the component
      let _filterCategories = this.filterCategories;
      let _selectedFilters = this.$store.getters.selectedFilters;
      let sortedSelectedFilters = {};

      Object.keys(this.filterCategories).forEach((filterCat) => {
        sortedSelectedFilters[filterCat] = _filterCategories[
          filterCat
        ].filter((selector) => _selectedFilters[filterCat].includes(selector));
      });

      //return this.$store.getters.selectedFilters;
      return sortedSelectedFilters;
    },
    filterCategories: function() {
      return this.$store.getters.filterCategories;
    },
    seriesName: function() {
      let _series = this.series;
      return [...new Set(_series.map((item) => item["name"]))];
    },
    searchString: function() {
      return this.$store.state.searchString;
    },
    data: function() {
      let data = this.$store.state.data.filter(
        (item) => item.earnings_growth != ""
      );
      console.log("data", data);
      return this.$store.state.data;
    },
    resetRangeSlider: function() {
      return this.$store.state.resetRangeSlider;
    },
    yearsSinceGraudated: function() {
      console.log(
        "yearsSinceGraduated",
        this.$store.getters.yearsSinceGraduated
      );
      return this.$store.getters.yearsSinceGraduated;
    },
    lang() {
      return this.$root.$i18n.locale;
    },
  },
  watch: {
    series: function() {
      let _this = this;
      _this.$refs.elmChart.updateOptions(_this.chartOptions);
      this.noDataLegend();
    },
    chartType: function() {
      let _this = this;
      let chartType = this.chartType;
      if (this.chartType == "earnings_growth") {
        _this.growthMode();
        _this.$refs.elmChart.updateOptions(this.chartOptions);
        console.log("earnings growth");
      } else if (this.chartType == "earnings_level") {
        _this.levelMode();
        _this.$refs.elmChart.updateOptions(this.chartOptions);
        console.log("earnings level");
      }
    },
    lang: {
      handler() {
        //console.log("chart lang", newVal);
        this.initChart();
      },
    },
  },
  methods: {
    updateChart: function() {
      this.filteredDataOne;
    },
    seriesIcon: function(index) {
      //console.log('series',this.series)
      return assignIcon(index);
    },
    seriesColor: function(seriesIndex) {
      let colorsArray = ["#008FFB", "#00E396", "#FEB019", "#FF4560", "#775DD0"];
      let arrayLength = colorsArray.length;

      if (seriesIndex >= arrayLength) {
        seriesIndex = seriesIndex - arrayLength;
        seriesIndex =
          seriesIndex - arrayLength * Math.floor(seriesIndex / arrayLength);
      }

      return colorsArray[seriesIndex];
    },
    filteredDataOne: function() {
      let _this = this;
      let filterObj = this.selectedFilters;
      let filterString = "";
      const filterKeys = Object.keys(filterObj);
      let _searchString = this.searchString;
      let data = this.data;

      let getData = new Promise(function(resolve, reject) {
        for (var key in filterKeys) {
          let _key = filterKeys[key];
          let keyFilterArray = filterObj[_key];

          if (keyFilterArray.length > 0) {
            let _filterString = "";

            // create filter string will list all filters and include || between each filter category selectors
            keyFilterArray.forEach((el, index) => {
              _filterString += `_data["${_key}"] == "${el}"`;
              if (keyFilterArray.length - 1 > index) {
                _filterString += " || ";
              }
            });
            filterString += `(${_filterString})`;
          }
          // if not the last search key will add '&&'
          if (filterKeys.length - 1 > key && keyFilterArray.length > 0) {
            filterString += " && ";
          }
        }

        let stringChk = filterString.substring(
          filterString.length - 3,
          filterString.length
        );
        if (stringChk == "&& ") {
          filterString = filterString.slice(0, filterString.length - 3);
        }

        if (filterString) {
          _searchString = `${filterString}`;
          //console.log('filterString',_searchString)
          let newData = data.filter((_data) => {
            return eval(_searchString);
          });
          _this.filteredDataArray = newData;
          _this.$store.state.newData = newData;
          //console.log("newData");
        } else {
          _this.filteredDataArray = this.data;
          _this.$store.state.newData = this.data;
          //console.log("same - newData");
        }

        resolve("data has been filtered");
      });

      getData.then(function() {
        //console.time("filter objects");
        _this.filterObjects();
        //console.timeEnd("filter objects");
      });
    },
    filterObjects: function() {
      // create individual search strings for each variation into a filterObject
      let _this = this;
      let _selectedFilters = this.selectedFilters;
      let _filterObjects = [];
      let pse_filter = _selectedFilters["pse_credential"];
      let cohort_filter = _selectedFilters["cohort"];
      let fos_filter = _selectedFilters["fos"];
      let gender_filter = _selectedFilters["gender"];
      let immcode_filter = _selectedFilters["immcode"];

      let createFilterObjArray = new Promise(function(resolve, reject) {
        // cohort / pse pse_credential
        cohort_filter.forEach((cohort) => {
          pse_filter.forEach((pse) => {
            fos_filter.forEach((fos) => {
              gender_filter.forEach((gender) => {
                immcode_filter.forEach((immcode) => {
                  let obj = {
                    cohort: cohort,
                    pse_credential: pse,
                    fos: fos,
                    gender: gender,
                    immcode: immcode,
                  };
                  _filterObjects.push(obj);
                });
              });
            });
          });
        });
        resolve("done");
      });

      createFilterObjArray.then(function() {
        _this.createSeries(_filterObjects);
      });
    },
    createSeries: function(_filterObjects) {
      let _this = this;
      let data = this.filteredDataArray;
      let newSeries = [];
      let chartType = this.chartType;
      let placeholder = [null, null, null, null, null];

      // filter for each filter obj
      _filterObjects.forEach((obj) => {
        let filterString = `_data["cohort"]=="${obj.cohort}" && _data["pse_credential"] == "${obj.pse_credential}" && _data["fos"]=="${obj.fos}" && _data["gender"] == "${obj.gender}" && _data["immcode"] == "${obj.immcode}"`;
        //console.log('filterOjb',filterString)

        // array of filtered objs
        let newArray = data.filter((_data) => {
          return eval(filterString);
        });

        // create series data array
        let seriesData = newArray.map(function(item) {
          //if null value change to 0
          if (typeof item[chartType] == "undefined") {
            return null;
          } else {
            return item[chartType];
          }
        });
        // create count data array
        let countData = newArray.map(function(item) {
          //if null value change to 0
          if (typeof item.count == "undefined") {
            return null;
          } else {
            return item.count;
          }
        });

        if (seriesData.length < 5) {
          placeholder = placeholder.slice(0, 5 - seriesData.length);
          seriesData = seriesData.concat(placeholder);
        }

        //console.log('newArray',newArray)
        newSeries.push({
          name: JSON.stringify(obj),
          data: seriesData,
          count: countData,
        });
      });
      window.localStorage.setItem("newSeries", JSON.stringify(newSeries));
      this.series = newSeries;
      //console.log(newSeries);
    },
    SeriesNameLegend: function(name) {
      let data = JSON.parse(name);
      let _selectedFilters = this.selectedFilters;
      // name templates
      let seriesName = "";
      let _cohort = this.$i18n.t(`cohort_selectors["${data.cohort}"]`);
      let _pse_credential = this.$i18n.t(
        `pse_credential_selectors["${data.pse_credential}"]`
      );
      let _fos = this.$i18n.t(`fos_selectors["${data.fos}"]`);
      let _gender = this.$i18n.t(`gender_selectors["${data.gender}"]`);
      let _immcode = this.$i18n.t(`immcode_selectors["${data.immcode}"]`);

      let defaultTemplate = `${_cohort} - ${_pse_credential} - ${_fos}`;
      let gender = `${_cohort} - ${_pse_credential} - ${_fos} - ${_gender}`;
      let immcode = `${_cohort} - ${_pse_credential} - ${_fos} - ${_immcode}`;

      if (
        _selectedFilters["gender"].length > 1 ||
        !_selectedFilters["gender"].includes("Both sexes")
      ) {
        seriesName = gender;
      } else if (
        _selectedFilters["immcode"].length > 1 ||
        !_selectedFilters["immcode"].includes("All student types")
      ) {
        seriesName = immcode;
      } else {
        seriesName = defaultTemplate;
      }

      return seriesName;
    },
    growthMode: function() {
      this.chartOptions.title.text = this.$i18n.t("earnings_growth_title");
      this.chartOptions.xaxis.title.text = this.$i18n.t("xaxis_title");

      let _this = this;
      let original = this.chartOptions;
      original.chart.type = "line";
      this.chartOptions = Object.assign(original, {
        yaxis: {
          seriesName: "Earnings Growth",
          min: 0,
          max: 0.25,
          tickAmount: 10,
          labels: {
            formatter: (value) => {
              if (_this.$i18n.locale == "fr") {
                return `${(value * 100).toFixed(0)} %`;
              } else {
                return `${(value * 100).toFixed(0)}%`;
              }
            },
            style: {
              color: undefined,
              fontSize: "14px",
              fontFamily: "Gotham Bold",
              cssClass: "apexcharts-yaxis-label",
            },
          },
        },
        tooltip: {
          y: {
            title: {
              formatter: function(seriesName, value) {
                let name = JSON.parse(seriesName);

                // DATA TO GET COUNT FOR DATA POINT
                // get seriesData from local stroage and parse to Object
                let _series = JSON.parse(
                  window.localStorage.getItem("newSeries")
                );
                // get count from array
                let count =
                  _series[value.seriesIndex].count[value.dataPointIndex];

                let _cohort = _this.$i18n.t(
                  `cohort_selectors["${name.cohort}"]`
                );
                let _pse_credential = _this.$i18n.t(
                  `pse_credential_selectors["${name.pse_credential}"]`
                );
                let _fos = _this.$i18n.t(`fos_selectors["${name.fos}"]`);
                let _gender = _this.$i18n.t(
                  `gender_selectors["${name.gender}"]`
                );
                let _immcode = _this.$i18n.t(
                  `immcode_selectors["${name.immcode}"]`
                );

                return `<strong>${_this.$i18n.t(
                  "cohort"
                )}: </strong>${_cohort}<br/>
                  <strong>${_this.$i18n.t(
                    "pse_credential"
                  )}: </strong>${_pse_credential}<br/>
                  <strong>${_this.$i18n.t("fos")}: </strong>${_fos}<br/>
                  <strong>${_this.$i18n.t("gender")}: </strong>${_gender}<br/>
                  <strong>${_this.$i18n.t("immcode")}: </strong>${_immcode}<br/>
                  <strong>${_this.$i18n.t("Count")}: </strong>${count}<br/>
                  <strong>${_this.$i18n.t("earnings_growth")}:</strong>`;
              },
            },
          },
          fillSeriesColor: false,
          theme: "light",
          style: {
            fontSize: "12px",
            fontFamily: undefined,
          },
          onDatasetHover: {
            highlightDataSeries: false,
          },
          fixed: {
            enabled: false,
            position: "topRight",
            offsetX: 0,
            offsetY: 0,
          },
        },
      });
      this.$refs.elmChart.updateOptions(this.chartOptions);
      this.initSlider();
      this.filteredDataOne();
    },
    levelMode: function() {
      let _this = this;
      this.chartOptions.title.text = this.$i18n.t("earnings_level_title");
      this.chartOptions.xaxis.title.text = this.$i18n.t("xaxis_title");
      this.chartOptions.yaxis = {
        min: 0,
        max: 140000,
        tickAmount: 10,
        seriesName: "<h1>Earning Level<h1>",
        labels: {
          formatter: (value) => {
            let currency = Window.lmicElm.$i18n
              .n(value, "currency")
              .replace("CA", "")
              .replace(".00", "");
            return currency;
          },
          style: {
            color: undefined,
            fontSize: "14px",
            fontFamily: "Gotham Bold",
            cssClass: "apexcharts-yaxis-label",
          },
        },
      };

      this.chartOptions.tooltip.y.title.formatter = function(
        seriesName,
        value
      ) {
        let name = JSON.parse(seriesName);

        // DATA TO GET COUNT FOR DATA POINT
        // get seriesData from local stroage and parse to Object
        let _series = JSON.parse(window.localStorage.getItem("newSeries"));
        // get count from array
        let count = _series[value.seriesIndex].count[value.dataPointIndex];

        let _cohort = _this.$i18n.t(`cohort_selectors["${name.cohort}"]`);
        let _pse_credential = _this.$i18n.t(
          `pse_credential_selectors["${name.pse_credential}"]`
        );
        let _fos = _this.$i18n.t(`fos_selectors["${name.fos}"]`);
        let _gender = _this.$i18n.t(`gender_selectors["${name.gender}"]`);
        let _immcode = _this.$i18n.t(`immcode_selectors["${name.immcode}"]`);

        return `<strong>${_this.$i18n.t("cohort")}: </strong>${_cohort}<br/>
                  <strong>${_this.$i18n.t(
                    "pse_credential"
                  )}: </strong>${_pse_credential}<br/>
                  <strong>${_this.$i18n.t("fos")}: </strong>${_fos}<br/>
                  <strong>${_this.$i18n.t("gender")}: </strong>${_gender}<br/>
                  <strong>${_this.$i18n.t("immcode")}: </strong>${_immcode}<br/>
                  <strong>${_this.$i18n.t("Count")}: </strong>${count}<br/>
                  <strong>${_this.$i18n.t("earnings_level")}:</strong>`;
      };
      this.$refs.elmChart.updateOptions(this.chartOptions);
      this.initSlider();
      this.filteredDataOne();
    },
    noDataLegend: function() {
      const noDataSeries = this.series.filter((item) => {
        item.data.reduce((a, b) => a + b, 0) == 0;
      });
      //console.log("nodata", noDataSeries);
    },
    updateYaxis: function(range) {
      // updates chart when range slider changes

      if (this.chartType == "earnings_level") {
        if (this.$i18n._vm.locale == "fr") {
          range[0] = range[0]
            .toString()
            .replace(",", ".")
            .replace(" ", "");
          range[1] = range[1]
            .toString()
            .replace(",", ".")
            .replace(" ", "");
        }
        this.chartOptions.yaxis[0].min = Number(
          range[0].replace(/[^0-9\.-]+/g, "")
        );
        this.chartOptions.yaxis[0].max = Number(
          range[1].replace(/[^0-9\.-]+/g, "")
        );
        this.$refs.elmChart.updateOptions(this.chartOptions, false, false);
      } else {
        if (range[0]) {
          let min = range[0].slice(0, -1);
          let max = range[1].slice(0, -1);
          this.chartOptions.yaxis[0].min = parseFloat(min) / 100;
          this.chartOptions.yaxis[0].max = parseFloat(max) / 100;
          this.$refs.elmChart.updateOptions(this.chartOptions, false, false);
        }
      }
    },
    toggleshadow: function(event) {
      var v = document.getElementById("chart-frame");
      if (event.shiftKey == true && v.className !== "card shadow") {
        v.className += " shadow";
      } else if (event.shiftKey == true && v.className == "card shadow") {
        v.classList.remove("shadow");
      }
    },
    initSlider() {
      let _this = this;
      let _slider = document.getElementById("vertical-range-slider");

      if (_slider.noUiSlider) {
        _slider.noUiSlider.destroy();
      }
      noUiSlider.create(_slider, this.rangeSliderOptions[this.chartType]);
      // Give the slider dimensions
      _slider.style.height = "475px";
      _slider.style.margin = "0 auto 15px";
      _slider.style.width = "8px";

      if (this.chartType == "earnings_growth") {
        // set slider handles to default
        setTimeout(function() {
          _slider.noUiSlider.set([0, 25]);
          _this.updateYaxis([0, 25]);
        }, 300);
      }

      let debounceUpdate = debounce(() => {
        this.$eventBus.$emit("updateChartUrl");
      }, 1000);

      // When the slider value changes, update the input and span
      _slider.noUiSlider.on("update", function(values, handle) {
        _this.updateYaxis(values);
        debounceUpdate();
      });
    },
    initChart() {
      let _this = this;
      if (this.chartType == "earnings_growth") {
        _this.growthMode();
      } else if (this.chartType == "earnings_level") {
        _this.levelMode();
      }
    },
  },
  updated() {
    this.$eventBus.$emit("updateChartUrl");
    //console.log("updated");
  },
  mounted() {
    //console.log("chart lang", this.$i18n.locale);

    // store watcher (watches for any changes to the selected filters )
    this.$store.watch(
      (state) => this.$store.state.selectedFilters,
      () => {
        //console.time("filter data one");
        this.filteredDataOne();
      },
      { deep: true }
    );
    this.$nextTick(function() {
      this.initChart();
    });
  },
  created() {
    this.$eventBus.$on("initChart", this.initChart);
    // get/set xaxis categories from data
    this.chartOptions.xaxis.categories = this.yearsSinceGraudated;
    this.chartOptions.xaxis.max = this.yearsSinceGraudated.length;
    this.chartOptions.xaxis.range = this.yearsSinceGraudated.length - 1; // -1 removes the blank starting tick on chart
  },
  beforeDestroy() {
    // global event bus 'off'
    this.$eventBus.$off("initChart");
  },
};
</script>

