import Vue from 'vue'
import Vuex from 'vuex'
import data from './assets/data/data.json'
const cloneDeep = require('clone-deep');
Vue.use(Vuex)

/* eslint-disable */

export default new Vuex.Store({
  state: {
    data: data,
    newData: [],
    showlang: false,
    selectedFilters: {
      pse_credential: [
        "College-level Certificate",
        "College-level Diploma",
        "Master's Degree",
        "Bachelor's Degree",
        "Doctoral Degree",
        "Professional Degree",
      ],
      cohort: [2010],
      gender: ["Both sexes"],
      immcode: ["All student types"],
      fos: ["All fields of study"],
      bos: []
    },
    defaultFilters: {
      pse_credential: [
        "College-level Certificate",
        "College-level Diploma",
        "Master's Degree",
        "Bachelor's Degree",
        "Doctoral Degree",
        "Professional Degree",
      ],
      cohort: [2010],
      gender: ["Both sexes"],
      immcode: ["All student types"],
      fos: ["All fields of study"],
      bos: []
    },
    searchString: '',
    resetRangeSlider: 'false'
  },
  mutations: {
    filteredData(state) {
      let filterObj = state.selectedFilters;
      let filterString = '';
      const filterKeys = Object.keys(filterObj)
      let _searchString = state.searchString;
      let data = state.data;

      setTimeout(function () {
        for (var key in filterKeys) {
          let _key = filterKeys[key]
          let keyFilterArray = filterObj[_key]


          if (keyFilterArray.length > 0) {
            let _filterString = '';

            // create filter string will list all filters and include || between each filter category selectors
            keyFilterArray.forEach((el, index) => {
              _filterString += `_data["${_key}"] == "${el}"`;
              if (keyFilterArray.length - 1 > index) {
                _filterString += " || ";
              }
            })
            filterString += `(${_filterString})`
          }
          // if not the last search key will add '&&'
          if (filterKeys.length - 1 > key && keyFilterArray.length > 0) {
            filterString += ' && ';
          }
        }

        let stringChk = filterString.substring(filterString.length - 3, filterString.length);
        if (stringChk == '&& ') {
          filterString = filterString.slice(0, filterString.length - 3)
        }

        if (filterString) {
          _searchString = `${filterString}`;
          //console.log('filterString',_searchString)  
          let newData = data.filter(_data => {
            return eval(_searchString);
          });


          state.newData = newData
          //console.log('data',state.newData)
          return newData;
          //return `${filterString}`;
        } else {
          state.newData = data
          return state.data;
        }
      }, 500)

    },
    resetFilters(state) {
      state.selectedFilters = state.defaultFilters;
    },
    updateSelectedFilters(state, payload) {
      state.selectedFilters = payload;
    }
    // reorderFilters(state){
    //   let _selectedFilters = state.selectedFilters;

    // },
    // updateData(state){
    //   console.log(state.selectedFilters)
    // },
  },
  actions: {

  },
  getters: {
    selectedFilters: state => {
      let _selectedFilters = state.selectedFilters;
      let sortedFilters = {};

      const keys = Object.keys(_selectedFilters)
      for (const key of keys) {
        sortedFilters[key] = _selectedFilters[key].sort();
      }
      state.selectedFilters = sortedFilters;
      return state.selectedFilters;
    },
    pseCredentialCategories: state => {
      //console.log('search cat',[...new Set(state.searchList.map(item => item['Filter_Category']))])
      return [...new Set(state.data.map(item => item['pse_credential']))];
    },
    cohortCategories: state => {
      //console.log('search cat',[...new Set(state.searchList.map(item => item['Filter_Category']))])
      return [...new Set(state.data.map(item => item['cohort']))];
    },
    yearsSinceGraduated: state => {
      return [...new Set(state.data.map(item => item['yrs_since_grad']))];
    },
    genderCategories: state => {
      //console.log('search cat',[...new Set(state.searchList.map(item => item['Filter_Category']))])
      return [...new Set(state.data.map(item => item['gender']))];
    },
    immcodeCategories: state => {
      //console.log('search cat',[...new Set(state.searchList.map(item => item['Filter_Category']))])
      return [...new Set(state.data.map(item => item['gender']))];
    },
    fos_order: state => {

      let copiedObject = _.cloneDeep(state.data)
      return copiedObject.sort((a, b) => (a.fos_order > b.fos_order ? 1 : -1));
    },
    filterCategories: (state, getters) => {
      // filters data into unqiue category filters
      //let _data = getters.fos_order
      let _data = cloneDeep(state.data);

      state.filterCategories = {
        // acquires all the unique values for each filter category
        "pse_credential": [...new Set(state.data.map(item => item['pse_credential']))],
        "cohort": [...new Set(state.data.map(item => item['cohort']))],
        "gender": [...new Set(state.data.map(item => item['gender']))],
        "immcode": [...new Set(state.data.map(item => item['immcode']))],
        "fos": [...new Set(_data.map(item => item['fos']))]
      }

      return state.filterCategories;
    },
  },
})